*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto-Medium"), local("Roboto Medium"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local("RobotoCondensed-Regular"), local("Roboto Condensed Regular"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local("RobotoCondensed-Bold"), local("Roboto Condensed Bold"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: local("PTMono-Regular"), local("PT Mono Regular"), url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.clear-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.clear-link {
  font: inherit;
  color: inherit;
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}
.container--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 110px 35px 50px;
}
@media (max-width: 1023px) {
  .container--header {
    padding: 20px 70px 25px 50px;
  }
}
@media (max-width: 767px) {
  .container--header {
    padding: 10px 25px 20px 35px;
  }
}

/* BLOCKS */
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background: linear-gradient(180deg, #041827 85%, rgba(255, 255, 255, 0) 100%);
  color: #f2f5f6;
}
@media (max-width: 767px) {
  .header {
    background: linear-gradient(180deg, #041827 70%, rgba(255, 255, 255, 0) 100%);
  }
}

@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.info {
  display: flex;
  gap: 30px;
}
@media (max-width: 1023px) {
  .info {
    align-self: flex-start;
    gap: 20px;
  }
}

@media (min-width: 1024px) {
  .info__item {
    width: 225px;
  }
}

.info__link {
  display: flex;
  padding: 10px 0;
  width: fit-content;
  gap: 15px;
}
@media (max-width: 1023px) {
  .info__link {
    padding: 10px;
  }
}

.info__icon {
  display: block;
}

.info__text {
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  font-family: "PT Mono", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .info__text {
    display: none;
  }
}

@media (max-width: 767px) {
  .logo__img {
    margin-top: 5px;
    height: auto;
    width: 35px;
  }
}

.accordion {
  margin: 0 auto 50px auto;
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  margin-bottom: 34px;
  padding: 10px;
}
.accordion__element.is-active > .accordion__button {
  opacity: 0.5;
}
.accordion__button {
  width: 100%;
  padding: 10px;
  text-transform: uppercase;
  cursor: pointer;
}
.accordion__wrapper {
  padding: 10px;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}